// Gatsby supports TypeScript natively!
import React from "react"
// import Layout from "../components/layout"
import SEO from "../components/seo"
import { graphql, Link } from "gatsby"
import { ModalRoutingContext } from "gatsby-plugin-modal-routing"
import BackgroundImage from "gatsby-background-image"
// import SingleHeader from "../components/SingleHeader"
// import { ReadMoreAndLess } from 'react-read-more-less';
// import ViewMoreText from 'react-native-view-more-text';
// import ShowMoreText from 'react-show-more-text';
// import ReactReadMoreReadLess from "react-read-more-read-less";
import Header from "../components/header"
import Footer from "../components/Footer"


export default ({ data, pageContext }) => {
  const post = data.markdownRemark
  // const htmlContent = { __html: post.html };
  const prev = pageContext.prev
    ? {
      url: `/${pageContext.prev.fields.slug}`,
      title: pageContext.prev.frontmatter.title,
    }
    : null

  const next = pageContext.next
    ? {
      url: `/${pageContext.next.fields.slug}`,
      title: pageContext.next.frontmatter.title,
    }
    : null

  return (
    <ModalRoutingContext.Consumer>
      {({ modal, closeTo }) => (
        <div>
          {modal ? (
            <Link to={closeTo} className="focus:outline-none">
              <div className="flex justify-end items-center text-xl text-gray-800 border-transparent">
                <button type="button border-transparent">âœ–</button>
              </div>
            </Link>
          ) : (
            <header>
              <Header className={`bg-gray-800`} />
            </header>
          )}

          <SEO title="Jelajahi Sumedang - Virtual Tourism" />

          <div className="bg-white">
            <div className="mx-auto max-w-4xl py-16 px-6 sm:py-24 lg:flex lg:max-w-7xl lg:items-center lg:justify-between lg:px-8">
              <h2 className="text-4xl font-bold tracking-tight text-gray-900 sm:text-4xl">
                <span className="block text-base">Virtual Tour</span>
                <span className="-mb-1 block text-black bg-clip-text pb-1 text-transparent">
                  {post.frontmatter.title}
                </span>
              </h2>
              <div className="mt-6 space-y-4 sm:flex sm:space-y-0 sm:space-x-5">
            <Link to={post.frontmatter.url} target="_blank" type="button" class="inline-flex items-center rounded-full border border-transparent bg-yellow-400 px-6 py-3 text-base font-medium text-gray-800 shadow-sm hover:bg-yellow-500 focus:outline-none focus:ring-offset-2">
                  Jelajahi
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-6 h-6">
                    <path stroke-linecap="round" stroke-linejoin="round" d="M4.5 19.5l15-15m0 0H8.25m11.25 0v11.25" />
                  </svg>

                </Link>
              </div>
            </div>
          </div>

          <BackgroundImage
            Tag="section"
            className={`w-full sm:h-96 h-60 flex-none bg-cover overflow-hidden sm:mb-4`}
            style={{
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
              height: "540px",
              width: "100%",
            }}
            fluid={post.frontmatter.thumbnailImage.childImageSharp.fluid}
          ></BackgroundImage>



          <div className="mx-auto max-w-7xl py-16 px-6 lg:py-20 lg:px-8">
            <div className="lg:grid lg:grid-cols-3 lg:gap-8">
              <div className="space-y-4">
                <h2 className="text-3xl font-bold tracking-tight text-gray-900">Lokasi</h2>
                <p className="text-lg text-gray-500">
                  Jl. Raya Rancakalong No.16, Nagarawangi, Kec. Rancakalong, Kabupaten Sumedang, Jawa Barat 45361
                </p>
              </div>
              <div className="mt-12 lg:col-span-2 lg:mt-0">
                <dl className="space-y-12">
                  <div
                    className="blog-post-content"
                    dangerouslySetInnerHTML={{ __html: post.html }}
                  />

                </dl>
              </div>
            </div>
          </div>






          <Footer />

        </div>
      )}
    </ModalRoutingContext.Consumer>

  )
}

export const query = graphql`
  query WisataBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        url
        thumbnailImage {
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`